@import url(https://fonts.googleapis.com/css?family=Lora:regular,bold,italic);
@import url(https://fonts.googleapis.com/css?family=Montserrat:regular,bold,italic);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{height:100vh}.App a{text-decoration:none}.App .sections{width:100%;height:100%}



.navbar{background-color:white;width:100%;height:65px;position:fixed;top:0;box-shadow:1px 1px 4px #7f5463;opacity:0.8;display:flex;justify-content:center;align-items:center;font-size:1.2rem;position:fixed;top:0;z-index:999}.navbar-container{display:flex;justify-content:center;align-items:center;height:6vw;max-height:75px;max-width:1500px}.navbar-container p{font-family:"Lora";font-weight:bold;font-size:30px;color:black}.navbar-logo{color:#fff;margin-left:20px;justify-self:start;cursor:pointer;text-decoration:none;font-size:2rem;display:flex;align-items:center}.navbar-logo img{height:50px;width:50px}.navbar-logo p{margin-left:10px}.nav-menu{display:grid;grid-template-columns:repeat(5, auto);grid-gap:10px;list-style:none;text-align:center;width:60vw;justify-content:end;top:65px}.nav-menu .nav-item{height:65px}.nav-menu .nav-item .nav-links{color:black;display:flex;align-items:center;text-decoration:none;height:100%;font-size:20px;padding:0.5rem 1rem;font-family:"Montserrat"}.nav-menu .nav-item .nav-links:hover{-webkit-text-decoration:underline #7f5463;text-decoration:underline #7f5463}.fa-bars{color:#fff}.nav-links-mobile{display:none}.menu-icon{display:none}@media screen and (max-width: 750px){.NavbarItems{position:relative}.navbar{background:#242424;height:20vw;max-height:60px;width:100%;display:flex;justify-content:center;align-items:center;font-size:15vw;position:fixed;top:0;z-index:999}.logoimg{position:relative;height:auto;width:auto;display:block;max-height:50px;top:0;left:0;-webkit-transform:translate(-300%, 0%);transform:translate(-300%, 0%)}.nav-menu{height:calc(100vh - min(60px, 20vw));width:300px;background-color:#737373;position:fixed;right:-200%;z-index:1;display:flex;flex-direction:column;align-items:center;opacity:1;transition:all 1s ease}.nav-menu.active{right:0;transition:all 1s ease;z-index:1;height:calc(100vh - min(60px, 20vw))}.nav-item{height:20vw;max-height:75px;align-items:center}.nav-links{text-align:center;align-items:center;width:300px;display:table;height:10vw;margin-top:30px}.nav-links:hover{background-color:#fff;color:#242424;border-radius:0}.menu-icon{display:block;position:absolute;top:0;right:0;-webkit-transform:translate(-100%, 30%);transform:translate(-100%, 30%);font-size:1.8rem;cursor:pointer}.fa-times{color:#fff;font-size:2rem;display:flex}.nav-links-mobile{display:block;text-align:center;border-radius:4px;width:80%;text-decoration:none;font-size:50vw;background-color:#242424;color:#fff;transition:all 0.3s ease-out}.nav-links-mobile:hover{background:#fff;color:#242424;transition:250ms}}

.home{background-color:#f2f2f2;height:100vh;display:flex}.home .left{flex:0.5 1;overflow:hidden;position:relative}.home .left .img-container{bottom:0;width:40vw;height:40vw;background-color:#7f5463;border-radius:50%;display:flex;align-items:flex-end;justify-content:center;float:right;position:absolute}.home .left .img-container img{width:100%}.home .right{flex:0.5 1;position:relative}.home .right .wrapper{width:100%;height:100%;display:flex;flex-direction:column;padding-left:50px;justify-content:center}.home .right .wrapper h2{font-family:'Lora';font-size:35px;font-weight:normal;color:#7f5463}.home .right .wrapper h1{font-family:'Montserrat';font-weight:lighter;font-size:60px;margin:10px 0}.home .right .wrapper h3{font-family:'Lora';font-weight:normal;font-size:30px}.home .right .wrapper h3 span{color:#7f5463;font-size:inherit}.home .right .wrapper h3 .ityped-cursor{-webkit-animation:blink 1s infinite;animation:blink 1s infinite}@-webkit-keyframes blink{50%{opacity:1}100%{opacity:0}}@keyframes blink{50%{opacity:1}100%{opacity:0}}.home .right .wrapper .arrow-container{width:25px;-webkit-animation:bounce 1600ms infinite cubic-bezier(0.445, 0.05, 0.55, 0.95);animation:bounce 1600ms infinite cubic-bezier(0.445, 0.05, 0.55, 0.95);cursor:pointer;height:20px;position:absolute;bottom:20px;left:40%}.home .right .wrapper .arrow-down{height:3px;background:#7f5463;-webkit-transform:rotate(45deg);transform:rotate(45deg);-webkit-transform-origin:0% 0%;transform-origin:0% 0%;border-radius:5px}.home .right .wrapper .arrow-down:after{content:'';position:absolute;width:100%;height:100%;bottom:0;left:100%;border-radius:5px;background:#7f5463;-webkit-transform:rotate(-90deg);transform:rotate(-90deg);-webkit-transform-origin:0% 100%;transform-origin:0% 100%}@-webkit-keyframes bounce{50%{-webkit-transform:translateY(-15px);transform:translateY(-15px)}}@keyframes bounce{50%{-webkit-transform:translateY(-15px);transform:translateY(-15px)}}

